p,
a,
span,
input,
button,
textarea,
ul,
li {
  font-family: "degular-display", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 18px;
  letter-spacing: 0.5px;
}

p {
  margin: 0;
}

button,
a {
  cursor: pointer;
}

:root {
  --border-radius: 3px;
  --guide: 14px;
  --main-color: #5833c3;
  --light-grey-background: #f1f1f1;
  --light-grey: #ddd;
}

.container {
  padding: 2rem;
}

.row {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.col {
  display: flex;
  flex-direction: column;
}

.col.settings {
  width: 300px;
  gap: 0.5rem;
}

img.generated-img {
  width: 100%;
  border-radius: 3px;
  max-width: 600px;
}

.PromptInput {
  border-bottom: 1px solid var(--light-grey);
  display: flex;
  flex-direction: column;
}

.PromptInput textarea {
  border: 0;
  background-color: var(--light-grey-background);
  border-radius: 3px;
  resize: none;
  padding: 0.75rem;
  margin-bottom: 0.5rem;
}

.PromptInput textarea:focus-visible {
  outline: none;
}

.promptItem span {
  font-size: 12px;
  margin-left: 1rem;
  cursor: pointer;
  min-width: 50px;
}

textarea.PromptInput::placeholder {
  font-size: 12px;
}

li {
  cursor: grab;
}

ul.PromptList {
  list-style: none;
  margin: 0;
  padding: 0;
  border-bottom: 1px solid var(--light-grey);
  padding-bottom: 0.3rem;
}

li.promptItem {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 6px 10px;
  background-color: var(--light-grey-background);
  justify-content: space-between;
  border-radius: 3px;
  margin-bottom: 0.2rem;
}

li.promptItem.editing {
  outline: 1px solid var(--main-color);
}

li.promptItem.editing input {
  width: 100%;
}

.editingPromptItem {
  border: 0;
  background: transparent;
}

.editingPromptItem:focus-visible {
  outline: none;
  padding: 0;
  margin: 0;
}

.PromptsListActions {
  display: flex;
  background-color: #f1f1f1;
  padding: 1rem 0.5rem;
  align-items: center;
  justify-content: center;
}

.generateBtn {
  display: flex;
}

.generateBtn button {
  border: none;
  background-color: var(--main-color);
  color: #fff;
  padding: 0.5rem 0;
  width: 100%;
  border-radius: var(--border-radius);
}

.smartPromp__modal {
  position: absolute;
  left: 348px;
  background-color: #fff;
  box-shadow: 0px 0px 4px 0px #ababab;
  padding: 1rem;
  border-radius: 3px;
  width: 300px;
}

.modal_header {
  display: flex;
  justify-content: space-between;
}

.modal_header p {
  font-size: var(--guide);
  margin: 0;
}

.contextMenu {
  position: absolute;
  z-index: 100; /* Ensure it's above other elements */
  background-color: white;
  border: 1px solid #ddd;

  border-radius: 5px;
}

.contextMenu button {
  background: transparent;
  border: 0;
  color: red;
  transition: all 0.2s ease-in-out;
  font-size: 14px;
  margin: 0;
  padding: 6px 10px;
}

.contextMenu button:hover {
  background-color: red;
  color: #fff;
}

ul.photography-options {
  margin: 1rem 0;
  padding: 0;
  list-style: none;
}

ul.photography-options li {
  margin-bottom: 6px;
  padding: 6px 12px;
  border-radius: 6px;
  border: 1px solid #919191;
  transition: all 0.2s ease-in-out;
}

ul.photography-options li.active-now:hover {
  cursor: pointer;
  box-shadow: 0px 0px 3px 0px var(--main-color);
}

ul.photography-options li:hover {
  cursor: not-allowed;
}

p.options-title {
  font-weight: 600;
  border-bottom: 1px solid var(--main-color);
  padding: 10px 0 5px;
  margin-bottom: 1rem;
}

.ambientLightSelect {
  display: flex;
  justify-content: space-around;
}

.ambientLightSelect .option {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  border: 2px solid #fff;
}

.ambientLightSelect .option:hover {
  cursor: pointer;
}

.ambientLightSelect .option.selected_option {
  border: 1px solid var(--main-color);
  border-radius: 6px;
  box-shadow: 0px 0px 3px 0px var(--main-color);
}

.ambientLightSelect .option img {
  width: 50px;
}

.ambientLightSelect .option span {
  font-size: 14px;
  margin-top: 0.5rem;
  font-weight: 600;
}

input.slider {
  -webkit-appearance: none;
  appearance: none;
  width: 100%;
  height: 2px;
  border-radius: 1px;
  background: #202020;
  outline: none;
  -webkit-transition: 0.2s;
  transition: opacity 0.2s;
}

input.slider::-moz-range-thumb,
input.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 23px;
  height: 25px;
  border: 0;
  cursor: pointer;
  background-color: var(--main-color);
}

.lightTime {
  padding: 2rem 1rem;
}

.lightTime .titles,
.lightTime .icons {
  display: flex;
  justify-content: space-between;
}

.lightTime .titles {
  width: 105%;
  margin-left: -2%;
  margin-top: 0.5rem;
}

.icons img {
  width: 40px;
}

.icons img:last-child {
  width: 30px;
}

.titles .title {
  font-weight: 600;
  font-family: "degular-display", sans-serif;
}

.back-btn {
  background-color: transparent;
  border: 0;
  border-top: 1px solid #000;
  width: 100%;
  padding-top: 0.5rem;
  font-size: 14px;
  margin-top: 2rem;
}

.back-btn:hover {
  color: var(--main-color);
}

span.modalCloseBtn {
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .container {
    padding: 1rem;
  }

  .col.settings {
    width: 100%;
  }

  .smartPromp__modal {
    left: 0;
    right: 0;
    margin: auto;
  }
}
